html, body {
	background-color:  $bodycolor;
	color:  $standartcolor;
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 21px;
	letter-spacing: 0.03em;
}

body {
	min-height: 100vh;
	overflow-x: hidden; 
}


body.index-page {
	background: radial-gradient(151.2% 260.55% at -10.94% 135.63%, #C4E0FF 0%, #ECF5FF 38.35%, #F7FBFF 83.26%);
}

