.overflow-hidden {
	overflow: hidden;
}

::-moz-selection { background: $selectionbg; color: $selectioncolor; }
::selection { background: $selectionbg; color: $selectioncolor; }

.fancybox-content {

	&__screen {

		position: absolute;
		top: 0;
		left: 0;
		z-index: 400;		
		width: 100%;
		height: 100%;

		& img {
			display: block;			
			width: 100%;
			height: auto;
		}

	}

}

.frontend {

}

.frontend, .footer, .popups {

	::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 21px;
		color: #88ABDF;
		opacity: 1; /* Firefox */
	}

	:-ms-input-placeholder { /* Internet Explorer 10-11 */
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 21px;
		color: #88ABDF;
	}

	::-ms-input-placeholder { /* Microsoft Edge */
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 21px;
		color: #88ABDF;
	}

	/*input:-webkit-autofill,
	input:-webkit-autofill:hover, 
	input:-webkit-autofill:focus, 
	input:-webkit-autofill:active  {
		-webkit-box-shadow: 0 0 0 30px white inset !important;
	}

	input:-webkit-autofill {
		-webkit-text-fill-color: $white !important;
	}*/

	input:-webkit-autofill,
	input:-webkit-autofill:hover, 
	input:-webkit-autofill:focus,
	textarea:-webkit-autofill,
	textarea:-webkit-autofill:hover,
	textarea:-webkit-autofill:focus,
	select:-webkit-autofill,
	select:-webkit-autofill:hover,
	select:-webkit-autofill:focus {
		/*border: 1px solid green;*/
		-webkit-text-fill-color: $standartcolor;
		-webkit-box-shadow: 0 0 0px 1000px $white inset;
		/*transition: background-color 5000s ease-in-out 0s;*/
	}

	& b,
	& strong {
	    font-weight: 600 !important;
	}

	& a {
		color: rgba(86, 109, 169, 0.8);
		text-decoration: none;
		transition: opacity 0.25s linear;
	}

	& a:hover {
		color: $linkhovercolor;
		text-decoration: none;
	}

	& a:focus,
	& a:active {
		opacity: 0.5;
	}

	a[data-fancybox="intarface"],
	a[data-fancybox="intarface"]:hover,
	a[data-fancybox="intarface"]:focus,
	a[data-fancybox="intarface"]:active,
	a.fancybox-interface,
	a.fancybox-interface:hover,
	a.fancybox-interface:focus,
	a.fancybox-interface:active {
		opacity: 1;
	}

	& p {
		font-style: normal;
		font-weight: 600;
		font-size: 18px;
		line-height: 21px;
		margin-bottom: 20px;
	}

	& p.mobile {
		font-size: 16px;
		line-height: 19px;
	}

	& h1, & .h1,
	& h2, & .h2,
	& h3, & .h3,
	& h4 & .h4 {
		font-family: Gilroy;
		font-style: normal;
		font-weight: bold;
		font-size: 46px;
    	line-height: 56px;
		margin-bottom: 40px;
	}

	& h2, & .h2 {
		font-weight: 600;
		font-size: 34px;
		line-height: 41px;
		margin-top: 30px;
		margin-bottom: 30px;
	}

	& h3, & .h3 {
		font-weight: 600;
		font-size: 30px;
		line-height: 36px;
		margin-top: 25px;
		margin-bottom: 25px;
	}

	& h4, & .h4 {
		font-weight: 600;
		font-size: 26px;
		line-height: 32px;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.nobr { white-space: nowrap; }

	.list-type-1 {

		margin: 0;
		padding: 0;
		list-style: none;

		&__item {

			margin: 0 0 24px 0;
			padding: 0 0 0 42px;
			list-style: none;
			position: relative;
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 26px;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				top: 2px;
				left: 0;
				width: 22px;
				height: 22px;
				background: url('../img/sprite/bullet-check-icon.svg') 5px 7px no-repeat, $maingradient;
				border-radius: 100%;
			}

		}

	}

	.list-type-2 {

		margin: 0;
		padding: 0;
		list-style: none;

		&__item {

			margin: 0 0 32px 0;
			padding: 0 0 0 42px;
			list-style: none;
			position: relative;
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 26px;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				top: 2px;
				left: 0;
				width: 22px;
				height: 22px;
				background: url('../img/sprite/bullet-storm-icon.svg') 7px 4px no-repeat, $pinkgradient;
				border-radius: 100%;
			}

		}

	}

	& .btn {

		display: inline-block;
		font-style: normal;
		font-weight: 600;
		font-size: 16px;
		line-height: 19px;
		color: $white;
		text-decoration: none;
		background: transparent;
		border: none;
		padding: 15px 59px;
		border-radius: 135px;
		transition: all 0.15s ease-in-out;

		&:hover {
			color: $white;
			/*box-shadow: 0px 2px 3px rgba(58, 112, 216, 0.6);*/
		}

		&:focus {
			color: $white;
			box-shadow: none;
		}

	}

	& .btn-primary {

		color: $white;
		background: $maingradient;

		&:hover,
		&:focus {
			color: $white;
			background: $maingradientreverse;
		}
		
		&:focus {
			opacity: .7;
		}

	}

	& .btn-secondary {

		border: solid 2px #147CEE;
		color: #147CEE;
		transition: box-shadow 0.15s ease-in-out;
		padding: 13px 57px;

		&:hover,
		&:focus {
			color: $white;
			background: $maingradient;
			border: none;
			padding: 15px 59px;
		}

		&:hover {
			background: $maingradientreverse;
		}

		&:focus {
			opacity: .7;
		}

		&.--arrow-down {			

			& span {

				position: relative;
				display: inline-block;

				&:after {						
					content: '';
					display: inline-block;
					width: 14px;
					height: 14px;
					background: url('../img/sprite/arrow-down-solid-btn.svg') 0 0 no-repeat;
					margin-left: 10px;
					position: absolute;
					top: 3px;
					right: -23px;
					z-index: 100;
				}

			}

			&:hover,
			&:focus {

				/*padding: 15px 45px 15px 30px;*/

				& span {

					&:after {
						background: url('../img/sprite/arrow-down-solid-btn-hover.svg') 0 0 no-repeat;
					}

				}

			}

		}

	}

	& .btn-info {

		background: linear-gradient(180deg, rgba(86, 94, 255, 0) 10.94%, #2830DC 100%), #55A5FD;
		color: $white;

		&:hover {
			background: linear-gradient(0deg, rgba(86, 94, 255, 0) 10.94%, #2830DC 100%), #55A5FD;
		}
		
		&:focus {
			opacity: .7;
		}

	}

	& .btn-sm {
		font-style: normal;
		font-weight: 500;
		font-size: 12px;
		line-height: 15px;
		padding: 8px 24px;
	}

	& .btn-secondary.btn-sm {

		padding: 6px 22px;

		&:hover,
		&:focus {
			padding: 8px 24px;
		}

	}

	& .btn-success {

		color: #adc4d8;
		background: transparent;
		border: solid 2px #adc4d8;
		padding: 13px 59px;

		&:hover,
		&:focus {
			cursor: default;
			color: #adc4d8;
			background: transparent;
		}
		
		&:focus {
			opacity: 1;
		}

	}

	.rouble {
		font-family: 'Roboto', sans-serif;
	}

	& .gradient-button {
		
		display: inline-block;
		border-radius: 135px;
		background: $maingradient;
		padding: 2px;

		& > span {

			display: inline-block;
			background: $white;
			border-radius: 135px;

			& > span {

				display: inline-block;
				background: $maingradient;
				padding: 15px 59px;
				border-radius: 135px;
				font-style: normal;
				font-weight: 600;
				font-size: 16px;
				line-height: 19px;
				text-decoration: none;
				color: transparent;
				background-clip: text;
				text-fill-color: transparent;

			}

		}

		&.--footer {
			& > span {
				background: #364560;
			}
		}

		&.--light-blue {
			& > span {
				background: #F8FBFF;
			}
		}

		&:hover {

			background: $maingradientreverse;

			& span {
				color: $white;
				background: transparent;
			}

		}

	}

	& .category-menu {

		&.--sticky {
			height: 89px;
		}

		&__wrap {
			background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #F8FBFF;
			padding: 34px 0 34px;
			position: relative;
			z-index: 550;
		}

		&.--sticky .category-menu__wrap {
			position: fixed;
			top: 0;
			width: 100%;
			z-index: 600;
		}

		&__list {

			margin: 0;
			padding: 0;
			list-style: none;

			& .menu-list {

				&__item {

					margin: 0;
					padding: 0 20px;
					list-style: none;

					&:first-child {
						padding-left: 0;
					}

					&:last-child {
						padding-right: 0;
					}

					& a {

						font-style: normal;
						font-weight: 400;
						font-size: 15px;
						line-height: 18px;

						& a:hover {
							color: $linkhovercolor;
						}

					}

					&.--active {

						& > a,
						& > a:hover,
						& > a:focus  {
							color: $linkhovercolor;
							cursor: default;
							opacity: 1;
						}

					}

					&.--title {
						padding: 0;
						font-style: normal;
						font-weight: 600;
						font-size: 18px;
						line-height: 21px;
						margin-right: 80px;
					}

					&.--dropdown {

						position: relative;

						& > a:after {
							content: '';
							display: inline-block;
							background: url('../img/sprite/menu-arrow-down.svg') 0 0 no-repeat;
							width: 11px;
							height: 6px;
							position: relative;
							top: -1px;
							margin-left: 5px;
							transition: transform .2s;
						}

						& > div {

							display: none;
							position: absolute;
							padding-top: 20px;
							top: 19px;

							& > ul {

								margin: 0;
								padding: 14px 0;
								list-style: none;
								position: relative;
								background: $white;
								border: 1px solid rgba(173, 196, 216, 0.4);
								box-sizing: border-box;
								box-shadow: 0px 2px 2px rgba(65, 78, 101, 0.1);
								border-radius: 4px;

								&:before {
									content: '';
									display: block;
									position: absolute;
									top: -13px;
									left: 20px;
									width: 25px;
									height: 14px;
									background: url('../img/sprite/dropdown-menu-triangle-up.svg') 0 0 no-repeat;
								}

								& > li {

									margin: 0;
									padding: 0;
									list-style: none;

									& > a {

										display: block;
										padding: 5px 18px;
										font-style: normal;
										font-weight: 400;
										font-size: 14px;
										line-height: 24px;
										white-space: nowrap;

										&:hover {
											color: $standartcolor;
											background: $lightblue;
										}

									}

								}

							}

						}

					}

					&.--dropdown:hover {

						& > div {
							display: block;
						}

						& > a {

							color: $linkhovercolor;

							&:after {
								transform: rotate(180deg);
							}

						}

					}

				}

			}

		}

	}

	& .demo {

		background: #ECF5FF;
		padding: 56px 0 64px;

		& > .container {

		}

		&__left {
			max-width: 590px;			
		}

		&__right {
			flex: 0;
			padding-left: 88px;
		}

		&__picture {

			 img {
				max-width: 370px;
			}

		}

		&__title {
			font-size: 36px;
			line-height: 42px;
			font-weight: 600;
			margin-bottom: 8px;
		}

		&__description {
			font-style: normal;
			font-weight: 400;
			font-size: 18px;
			line-height: 24px;
			color: #7093C2;
			margin-bottom: 48px;
		}

		&__form {

			& .form-group {

				margin-bottom: 32px;

				&.--left {
					padding-right: 16px;
					margin-bottom: 0px;
					flex: 0 0 50%;
				}

				&.--right {
					padding-left: 16px;
					margin-bottom: 0px;
					flex: 0 0 50%;
				}

				&.--terms {

				}

				&.--submit {

					margin-bottom: 0;

					& button {
						padding: 15px 41px;
					}

				}

				& .form-control {

					background: $white;
					border: 1px solid #D0E6FF;
					border-radius: 5px;
					color: $standartcolor;
					font-style: normal;
					font-weight: 500;
					font-size: 18px;
					line-height: 21px;
					padding: 7px 14px;
					padding: 10px 14px;

					&:focus {
						box-shadow: none;
						border-color: #3696FF;
					}

				}

				& .form-check-label {

					font-style: normal;
					font-weight: 400;
					font-size: 14px;
					line-height: 17px;

					& a {

						color: $linkhovercolor;

						&:hover {
							text-decoration: underline;
						}

					}

				}

				& .form-check-input {

					background: $white;
					border: 1px solid #DAE4ED;
					border-radius: 4px;
					width: 20px;
					height: 20px;
					margin-right: 6px;
					margin-top: 1px;
					transition: none;
					cursor: pointer;

					&:checked[type="checkbox"] {
						background: $white url('../img/sprite/checkbox-checked.svg') center center no-repeat; 
					}

					&:checked {
						background-color: $white;
						border-color: #DAE4ED;
					}

					&:active {
						filter: none;
					}

					&:focus {
						box-shadow: none;
						border-color: #D0E6FF;
					}

					&:hover {
						border-color: #3696FF;
					}

				}

			}

		}

	}

	& .demo-access {

		background: #F8FBFF;
		position: relative;
		padding: 80px 0 64px;

		&:before,
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 33px;
			width: 100%;
			background: linear-gradient(180deg, #EEF5FF 0%, rgba(255, 255, 255, 0) 100%);
		}

		&:after {
			top: auto;
			bottom: 0;
			transform: rotate(-180deg);
		}

		& > .container {

			position: relative;

			&:before {
				content: '';
				display: block;
				position: absolute;
				top: -35px;
				left: 0;
				width: 203px;
				height: 199px;
				background: url('../img/sprite/demo-access-bg-left.svg') 0 0 no-repeat;

			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				top: -25px;
				right: 0;
				width: 205px;
				height: 178px;
				background: url('../img/sprite/demo-access-bg-right.svg') 0 0 no-repeat;
			}

		}

		&__title {
			font-size: 24px;
			line-height: 32px;
			font-weight: 600;
			margin: 0 auto 32px;
			max-width: 900px;
			position: relative;
			z-index: 200;
		}

		&__buttons {

		}

	}

	& .presentation-download {

		background: radial-gradient(151.2% 260.55% at -10.94% 135.63%, #C4E0FF 0%, #ECF5FF 38.35%, #F7FBFF 83.26%);
		padding: 48px 0 52px;

		&__picture {

			position: relative;

			&-screen {

