				width: 201px;
				height: 288px;
				background: rgba(255, 0, 0, 0.5);
				top: 53px;
				left: 12px;
				z-index: 400;

				& > img {
					display: block;
					width: 100%;
					height: 100%;
					transition: transform 0.25s linear;
				}

				&:hover {

					& > img {
