	& .contact-block {

		background: #F8FBFF;
		position: relative;
		padding: 80px 0 64px;

		&:before,
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 33px;
			width: 100%;
			background: linear-gradient(180deg, #EEF5FF 0%, rgba(255, 255, 255, 0) 100%);
		}

		&:after {
			top: auto;
			bottom: 0;
			transform: rotate(-180deg);
		}

		& > .container {

			position: relative;

