				display: block;
				position: absolute;
				top: -35px;
				left: -50px;
				width: 203px;
				height: 199px;
				background: url('../img/sprite/contact-block-bg-left.svg') 0 0 no-repeat;

			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				top: -25px;
				right: -50px;
				width: 205px;
				height: 178px;
				background: url('../img/sprite/contact-block-bg-right.svg') 0 0 no-repeat;
			}

		}

		&__title {
			font-size: 24px;
			line-height: 31px;
			margin: 0 auto 32px;
			font-weight: 600;
			position: relative;
			z-index: 200;
			max-width: 800px;
		}

		&__buttons {
			position: relative;
			z-index: 200;
		}

	}

	& .owl {

		&-nav {
			
			& button {
				outline: none;
				border: none;				
			}

		}

		&__nav-left,
		&__nav-right {

			width: 40px;
			height: 40px;
			background: #E2F1FF;
			border-radius: 100%;
			position: relative;
			cursor: pointer;

			&:before {
				content: '';
				display: block;
				width: 10px;
				height: 17px;
				position: absolute;
				top: 50%;
				left: 50%;
				-webkit-transform: translate(-50%, -50%);
				-moz-transform: translate(-50%, -50%);
				-ms-transform: translate(-50%, -50%);
				transform: translate(-50%, -50%);
			}

		}

		&__nav-left {

			&:before {
				background: url('../img/sprite/arrow-left-owl-carousel.svg') 0 0 no-repeat;
			}

		}

		&__nav-right {

			&:before {
				background: url('../img/sprite/arrow-right-owl-carousel.svg') 0 0 no-repeat;
			}

		}

		&-prev,
		&-next {
			transition: opacity 0.25s linear;
		}

		&-prev:hover,
		&-next:hover {
			opacity: 0.75;
		}

		&-prev.disabled,
		&-next.disabled,
		&-prev.disabled:hover,
		&-next.disabled:hover {
			display: none;
			cursor: default;
		}

	}

	& .owl-theme {

		& .owl-dots {
			position: absolute;
			bottom: -80px;
			left: 50%;
			transform: translateX(-50%);
			z-index: 220;
		}
		
		& .owl-nav {
			margin-top: 0;
		}
		
		& .owl-dots .owl-dot {
			outline: 0;
			border: none;
		}

		& .owl-dots .owl-dot span {
			width: 11px;
			height: 11px;
			margin: 0px 8px;
			background: $maingradient;
			border-radius: 100%;
			opacity: 0.3;
		}

		& .owl-dots .owl-dot:hover span {
			opacity: 0.7;
		}

		& .owl-dots .owl-dot.active span,
		& .owl-dots .owl-dot.active:hover span  {
			background: $maingradient;
			opacity: 1;
		}

		& .owl-nav [class*='owl-']:hover {
			background: transparent;
			color: transparent;
		}

	}

}

