.modus-ims {

	&__hero {

		padding: 186px 0 78px;
		background: radial-gradient(151.2% 260.55% at -10.94% 135.63%, #C4E0FF 0%, #ECF5FF 38.35%, #F7FBFF 83.26%);
		overflow: hidden;

		&-wrap {
			background: url('../images/modus/ims/modus-ims-hero-bg.svg') calc(50% + 50px) calc(50% - 128px) no-repeat;
		}

		& .hero-content {

			flex: 0 0 620px;
			padding-right: 50px;

			&__title {
				font-size: 46px;
				line-height: 56px;
				margin-bottom: 10px;
			}

			&__description {
				font-style: normal;
				font-weight: 500;
				font-size: 28px;
				line-height: 36px;
				margin-bottom: 29px;
			}

			&__text {

				font-size: 18px;
				line-height: 26px;
				font-weight: 400;
				color: #8091B5;
				margin-bottom: 75px;

				& b, & strong {
					color: $standartcolor;
				}

			}

			&__buttons {

				& > .btn:first-child {
					margin-right: 25px;
				}

				& > .btn:last-child {

					padding: 13px 45px 13px 30px;
					position: relative;

					&:focus {
						padding: 15px 45px 15px 30px;
					}

					& span {

						position: relative;

						&:after {						
							content: '';
							display: inline-block;
							width: 14px;
							height: 14px;
							background: url('../img/sprite/arrow-down-solid-btn.svg') 0 0 no-repeat;
							margin-left: 10px;
							position: absolute;
							top: 3px;
							right: -23px;
							z-index: 100;
						}

					}

					&:hover,
					&:focus {

						padding: 15px 45px 15px 30px;

						& span {

							&:after {
								background: url('../img/sprite/arrow-down-solid-btn-hover.svg') 0 0 no-repeat;
							}

						}

					}

				}

			}

		}

		& .hero-picture {

			flex: 1;
			position: relative;

			& > picture,
			& > img {
				display: block;
				/*max-width: 100%;*/
				max-width: 625px;
				height: auto;
				position: relative;
				right: -20px;
			}

			& > picture {
				position: relative;
			}

			& > a > picture > .hero-picture__screen {
				position: absolute;
				z-index: 400;
				max-width: 100%;
				max-height: 100%;
				/*left: 50%;
				transform: translateX(-50%);*/

				& > img {
					display: block;
					max-width: 100%;
					max-height: 100%;
				}
			}

		}

	}

	&__about {

		padding-top: 104px;
		padding-bottom: 120px;

		&-left {
			flex: 0 0 685px;
			padding-right: 104px;
		}

		&-right {
			flex: 0 0 171px;
		}

		&-title {
			font-size: 46px;
			line-height: 58px;
			font-weight: 700;
			margin-bottom: 10px;
		}

		&-description {
			font-style: normal;
			font-weight: 500;
			font-size: 28px;
			line-height: 36px;
			margin-bottom: 56px;
		}

		&-list {

			margin: 0;
			padding: 0 0 0 42px;
			/*padding: 0;*/
			list-style: none;

			& .about-list {

				&__item {

					margin: 0 0 24px 0;
					padding: 0 0 0 42px;
					list-style: none;
					position: relative;

					&:before {
						content: '';
						display: inline-block;
						position: absolute;
						top: 2px;
						left: 0;
						width: 22px;
						height: 22px;
						background: url('../img/sprite/bullet-check-icon.svg') 5px 7px no-repeat, $maingradient;
						border-radius: 100%;
					}

					font-style: normal;
					font-weight: 400;
					/*opacity: 0.6;*/
					font-size: 18px;
					line-height: 26px;
					/* or 144% */

					/*& b, & strong {
						opacity: 1;
					}*/

				}

			}

		}

		&-picture {

			position: relative;
			top: -50px;

			& picture,
			& img {
				max-width: 100%;
			}

		}

	}

	&__video {

		padding: 96px 0 112px;
		background: #D9E7FF;

		&-title {
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 20px;
			margin-bottom: 20px;
		}

		&-left {

			flex: 0 0 862px;

			& > div {

				width: 100%;
				padding-top: 50%;
				padding-top: 56.25%;
				position: relative;

				& > iframe {
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					z-index: 100;
				}

			}

		}

		&-right {
			flex: 1;
			padding-left: 67px;
		}

		&-heading {
			font-style: normal;
			font-weight: 600;
			font-size: 36px;
			line-height: 40px;
			margin-bottom: 15px;
			max-width: 360px;
		}

		&-text {

			font-style: normal;
			font-weight: 400;
			font-size: 18px;
			line-height: 26px;
			margin-bottom: 42px;
			max-width: 360px;
			/*color: #8091B5;*/

			& b, & strong {
				color: $standartcolor;
			}

		}

	}

	&__turnover {

		padding: 112px 0 144px;
		overflow: hidden;

		&-title {
			font-size: 30px;
			line-height: 40px;
			margin-bottom: 48px;
    		font-weight: 600;
		}

		&-list {

			margin: 0 auto 64px;
			padding: 0;
			list-style: none;
			max-width: 810px;

			& .turnover-list {

				&__item {

					flex: 0 0 50%;
					margin: 0 0 40px;
					padding: 0;
					list-style: none;

					&:nth-last-child(1),
					&:nth-last-child(2) {
						margin-bottom: 0px;
					}

					&:nth-child(odd) {
						padding-right: 20px;
					}

					&:nth-child(even) {
						padding-left: 20px;
					}

					&-gradient {
						padding: 1px;
						box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
						background: $maingradient;
						border-radius: 4px;
						height: 100%;
					}

					&-wrap {
						padding: 72px 40px 48px;
						background: $white;
						border-radius: 4px;
						text-align: center;
						height: 100%;
					}

					&-title {
						font-size: 42px;
						line-height: 56px;
						color: #1ECDD8;

						& span {
							display: inline-block;
							width: 55px;
						}

					}

					&-description {
						font-size: 20px;
						line-height: 26px;
						margin-bottom: 24px;
						color: #1ECDD8;
					}

					&-progressbar {

						width: 100%;
						height: 15px;
						background: rgba(173, 196, 216, 0.25);
						border-radius: 20px;
						position: relative;
						margin-bottom: 24px;

						& span {
							position: absolute;
							z-index: 100;
							top: 0;
							left: 0;
							height: 100%;
							width: 0%;
							border-radius: 20px;
							background: $maingradient;
							transition: width 2s ease-out;
						}

					}

					&-text {

						font-size: 15px;
						line-height: 21px;
						color: #8091B5;
						font-weight: 400;

						& strong,
						& b {
							color: #3E5284;
						}

					}

					&-notice {
						font-style: normal;
						font-weight: normal;
						font-size: 14px;
						line-height: 17px;
						color: #8091B5;
						margin-top: 18px;
					}

				}

			}

		}

		&-cards {

			margin: 0 auto;
			padding: 0;
			list-style: none;
			max-width: 747px;
			position: relative;
			z-index: 110;

			&:before {
				content: '';
				display: block;
				position: absolute;
				top: -21px;
				left: -124px;
				background: url('../img/sprite/turnover-cards-bg-left.svg') 0 0 no-repeat;
				width: 214px;
				height: 445px;
				z-index: 100;
			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				top: 230px;
				right: -124px;
				background: url('../img/sprite/turnover-cards-bg-right.svg') 0 0 no-repeat;
				width: 211px;
				height: 246px;
				z-index: 100;
			}

			& .turnover-cards {

				&__item {

					margin: 0 0 25px;
					padding: 43px 75px 51px 75px;
					list-style: none;
					background: $white;
					box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.07);
					position: relative;
					z-index: 110;

					&:last-child {
						margin-bottom: 0;
					}

					&:nth-child(odd):after {
						content: '';
						display: block;
						width: 60px;
						height: 60px;
						background: white;
						right: -30px;
						top: 50%;
						transform: translateY(-50%);
						position: absolute;
						z-index: 110;
						border-radius: 100%;
						box-shadow: inset 28px -1px 17px -21px rgba(0, 0, 0, 0.07);
					}

					&:nth-child(even):before {
						content: '';
						display: block;
						width: 60px;
						height: 60px;
						background: white;
						left: -30px;
						top: 50%;
						transform: translateY(-50%);
						position: absolute;
						z-index: 110;
						border-radius: 100%;
						box-shadow: inset -28px -1px 17px -21px rgba(0, 0, 0, 0.07);
					}

					&-picture {
						flex: 0 0 135px;

						& picture,
						& img {
							max-width: 100%;
						}
					}

					&-text {

						flex: 1;
						padding-left: 34px;
						font-size: 18px;
						line-height: 25px;
						font-weight: 400;
						color: #8091B5;
						max-width: 450px;

						& strong,
						& b {
							color: #3E5284;
						}

					}

				}

			} 

		}

	}

	&__express-diagnostic {

		background: #F8FBFF;
		position: relative;
		padding: 80px 0 64px;
		overflow: hidden;

		&:before,
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 33px;
			width: 100%;
			background: linear-gradient(180deg, #EEF5FF 0%, rgba(255, 255, 255, 0) 100%);
		}

		&:after {
			top: auto;
			bottom: 0;
			transform: rotate(-180deg);
		}

		& > .container {

			position: relative;

			&:before {
				content: '';
				display: block;
				position: absolute;
				top: -35px;
				left: 0;
				width: 80px;
				height: 153px;
				background: url('../img/sprite/express-diagnostic-bg-left.svg') 0 0 no-repeat;

			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				top: -25px;
				right: 0;
				width: 112px;
				height: 159px;
				background: url('../img/sprite/express-diagnostic-bg-right.svg') 0 0 no-repeat;
			}

		}

		& .express-diagnostic {

			&__title {
				font-size: 24px;
				line-height: 40px;
				margin-bottom: 32px;
				font-weight: 600;
				position: relative;
				z-index: 200;
			}

			&__buttons {

				position: relative;
    			z-index: 200;

				& a:first-child {
					margin-right: 24px;
					position: relative;
				}

			}

		}

	}

