	&__for-whom {

		padding: 96px 0;

		& .for-whom {

			&__wrap {

				/*padding-left: 150px;*/

				&.--first {
					max-width: 470px;
					margin: 0 auto;
				}

			}

			&__title {
				font-style: normal;
				font-weight: 700;
				font-size: 48px;
				line-height: 58px;
				margin-bottom: 10px;			
			}

			&__description {
				max-width: 560px;
				font-style: normal;
				font-weight: 500;
				font-size: 28px;
				line-height: 36px;
				margin-bottom: 48px;	
			}

			&__list {

				max-width: 510px;
				margin-bottom: 180px;
				/*padding: 0 0 0 42px;*/
				padding: 0;

				&-item {
					font-weight: 400;
					font-size: 18px;
					line-height: 26px;
				}

			}

			&__tabs {

				& .tabs {

					&__container {

					}

					&__nav {

						position: relative;

						&-left,
						&-right {

							content: '';
							display: block;
							/*width: 6px;*/
							width: 6px;
							height: 10px;
							position: absolute;
							z-index: 100;
							top: 6px;
							cursor: pointer;
							transition: all 0.25s linear;

							&:hover {
								opacity: 0.7;
								width: 16px;
							}

						}

						&-left {

							left: -22px;
							background: url('../img/sprite/tab-arrow-left.svg') left 0 no-repeat;

							&:hover {
								left: -32px;
							}

						}

						&-right {

							right: -22px;
							background: url('../img/sprite/tab-arrow-right.svg') right 0 no-repeat;

							&:hover {
